/*----------------------------------- fixes ----------------------------------*/
.remove-outline {outline: none !important; box-shadow: none !important;}

/*------------------------------- fix react-vis ------------------------------*/
.rv-discrete-color-legend-item {
  color: #f8f9fa !important;
}
.rv-discrete-color-legend-item.disabled {
  color:#6c757d !important;
}

.rv-discrete-color-legend-item.clickable:hover {
  border-radius: 2px;
  background: #252526 !important;
}

.rv-search-wrapper__form__input {
  background: transparent !important;
  border-color: #252526 !important;
}

/*--------------------------------- fix forms --------------------------------*/


/*----------------------------- device cards ---------------------------------*/
.card-hover {
  transition: box-shadow .3s, border-color .3s;
}
.card-hover:hover {
  border-color: rgba(0, 123, 255, .5) !important;
  box-shadow: 0 0 5px rgb(0, 123, 255); 
}
#myVideo {
  position: fixed;
  right: -15px; 
  top: -15px;
  min-width: calc(100vw + 30px);
  min-height: calc(100vh + 30px);
}

/*--------------------------------- borders ----------------------------------*/
.debug-border {
	border: 1px solid blue;
}

.border-x {
	border-right: 1px solid white;
	border-left: 1px solid white;
}

/*------------------------------ graph buttons -------------------------------*/
.btn-graph {
  display: block;
  position: absolute !important;
  top: -10px;
  left: 40px;
  z-index: 300;
  opacity: 0.4;
  transition: opacity 300ms;
}
.btn-graph:hover {
  opacity: 1;
}


/*---------------------------------- modal -----------------------------------*/
/* The Modal (background) */
.modal-bg {
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}
